
*
{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}
body
{
    min-height: 100vh;

}
.container
{
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}
.title
{
    padding: 10px;
    font-size: 1.5rem;
    margin: 6vh;
    border: 4px dashed rgb(1, 17, 160);
    border-radius: 20px;
}
.cards
{
    display: flex;
    flex-wrap: wrap;
    /* align-items: center; */
    justify-content: center;
    max-width: 1100px;
    margin: 0 auto;
}
.card
{
    width: 300px;
    height: max-content;
    margin: 1rem;
    padding: 1rem;
    display: flex;
    flex-direction: column;
    border-radius: 10px;
    align-items: center;
    box-shadow:rgba(0, 0, 0.24) 0px 3px 8px;
}
.card:hover{
    transform: scale(1.03);
    transform: 0.5s;
    transition: all 0.7s;
}
.image
{
    width: 285px;
    border-radius: 10px;
    aspect-ratio: 1/1;
    object-fit: cover;
}
.tour-info
{
    margin: 20px 5px;
}
.tour-price
{
    color: #1faa59;
    font-size: 1.3rem;
}
.tour-name
{
    font-size: 1.4rem;
}
.description
{
    font-size:1em;
}
.read-more
{
    color: #12b0e8;
    cursor: pointer;
}
.btn-red
{
    margin-top: 18px;
    padding: 10px 60px;
    border: 1px solid #b4161b;
    cursor: pointer;
    border-radius: 10px;
    font-size: 18px;
    font-weight: bold;
    background-color: #b4161b21;
}
.btn-red:hover
{
    background-color: #b4161b;
    color: white;
    transition: all 0.2s;
}
.refresh
{
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;  
}
.refresh h2
{
    font-weight: bold;
    font-family: Arial, Helvetica, sans-serif;
}
.btn-white
{
    margin-top: 18px;
    font-family: 'Courier New', Courier, monospace;
    padding: 10px 60px;
    border: 1px solid white;
    cursor: pointer;
}
.btn-white:hover
{
    background-color: white;
    color: black;
    transition: all 0.2s;
}

@media only screen and (max-width: 600px) {
    .title {
        padding: 10px;
        font-size: 1rem;
        margin: 5vh;
        border: 3px dashed rgb(1, 17, 160);
        border-radius: 20px;
    }
    /* .title {
        border:0px;
    } */
  }
